import React from "react";
import styled, { keyframes } from "styled-components";

// Анимации бордеров
const borderLeft = keyframes`
  0% { height: 0; }
  100% { height: 100%; }
`;

const borderBottom = keyframes`
  0% { width: 0; }
  100% { width: 100%; }
`;

const borderRight = keyframes`
  0% { height: 0; }
  100% { height: 100%; }
`;

const borderTop = keyframes`
  0% { width: 0; }
  100% { width: 100%; }
`;

const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
`;

const Overlay = styled.div`
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
`;

const Modal = styled.div`
  background-color: #111;
  padding: 3rem 2rem;
  border-radius: 8px;
  max-width: 500px;
  width: 100%;
  animation: ${fadeIn} 0.3s ease forwards;
  position: relative;
  box-sizing: border-box;

  @media (max-width: 768px) {
    padding: 2rem 1.5rem;
    max-width: 90%;
  }

  @media (max-width: 480px) {
    padding: 1.5rem 1rem;
  }
`;

const CloseButton = styled.button`
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 1.8rem;
  color: #ccc;
  cursor: pointer;
  transition: color 0.3s;

  &:hover {
    color: #800000;
  }
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
`;

const InputWrapper = styled.div`
  position: relative;
`;

const AnimatedInput = styled.input`
  width: 100%;
  padding: 0.75rem 1rem;
  background: #222;
  border: none;
  color: #eee;
  font-family: "Euclid Circular A", sans-serif;
  font-size: 1rem;

  &::placeholder {
    color: #777;
  }

  &:focus {
    outline: none;
  }

  & ~ span {
    position: absolute;
    background-color: #800000;
  }

  & ~ .border-left {
    top: 0;
    left: 0;
    width: 1px;
    height: 0;
  }

  & ~ .border-bottom {
    bottom: 0;
    left: 0;
    width: 0;
    height: 1px;
  }

  & ~ .border-right {
    top: 0;
    right: 0;
    width: 1px;
    height: 0;
  }

  & ~ .border-top {
    top: 0;
    left: 0;
    width: 0;
    height: 1px;
  }

  &:focus ~ .border-left {
    animation: ${borderLeft} 0.2s forwards;
  }
  &:focus ~ .border-bottom {
    animation: ${borderBottom} 0.2s 0.2s forwards;
  }
  &:focus ~ .border-right {
    animation: ${borderRight} 0.2s 0.4s forwards;
  }
  &:focus ~ .border-top {
    animation: ${borderTop} 0.2s 0.6s forwards;
  }
`;

const AnimatedTextarea = styled.textarea`
  width: 100%;
  padding: 0.75rem 1rem;
  background: #222;
  border: none;
  color: #eee;
  font-family: "Euclid Circular A", sans-serif;
  font-size: 1rem;
  resize: vertical;
  height: 150px;

  &::placeholder {
    color: #777;
  }

  &:focus {
    outline: none;
  }

  & ~ span {
    position: absolute;
    background-color: #800000;
  }

  & ~ .border-left {
    top: 0;
    left: 0;
    width: 1px;
    height: 0;
  }

  & ~ .border-bottom {
    bottom: 0;
    left: 0;
    width: 0;
    height: 1px;
  }

  & ~ .border-right {
    top: 0;
    right: 0;
    width: 1px;
    height: 0;
  }

  & ~ .border-top {
    top: 0;
    left: 0;
    width: 0;
    height: 1px;
  }

  &:focus ~ .border-left {
    animation: ${borderLeft} 0.2s forwards;
  }
  &:focus ~ .border-bottom {
    animation: ${borderBottom} 0.2s 0.2s forwards;
  }
  &:focus ~ .border-right {
    animation: ${borderRight} 0.2s 0.4s forwards;
  }
  &:focus ~ .border-top {
    animation: ${borderTop} 0.2s 0.6s forwards;
  }
`;

const SubmitButton = styled.button`
  padding: 0.75rem 1.5rem;
  border: 2px solid #800000;
  color: #800000;
  background-color: transparent;
  font-size: 1rem;
  font-family: "Euclid Circular A", sans-serif;
  cursor: pointer;
  transition: all 0.3s;
  margin-top: 1rem;
  border-radius: 4px;

  &:hover {
    background-color: #800000;
    color: #fff;
  }
`;

const ModalForm = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  const handleOverlayClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <Overlay onClick={handleOverlayClick}>
      <Modal>
        <CloseButton onClick={onClose}>&times;</CloseButton>
        <Form>
          <InputWrapper>
            <AnimatedInput type="text" placeholder="Ваше имя" required />
            <span className="border-left" />
            <span className="border-bottom" />
            <span className="border-right" />
            <span className="border-top" />
          </InputWrapper>

          <InputWrapper>
            <AnimatedInput type="email" placeholder="Ваш Email" required />
            <span className="border-left" />
            <span className="border-bottom" />
            <span className="border-right" />
            <span className="border-top" />
          </InputWrapper>

          <InputWrapper>
            <AnimatedTextarea placeholder="Ваше сообщение..." required />
            <span className="border-left" />
            <span className="border-bottom" />
            <span className="border-right" />
            <span className="border-top" />
          </InputWrapper>

          <SubmitButton type="submit">Отправить заявку</SubmitButton>
        </Form>
      </Modal>
    </Overlay>
  );
};

export default ModalForm;
