import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

const PageWrapper = styled.div`
  background-color: #fff;
  min-height: 100vh;
  color: #000;
  padding: 2rem 30px;
`;

const Gallery = styled.div`
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;

  @media (max-width: 768px) {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
`;

const PhotoCard = styled.div`
  text-align: center;

  img {
    width: 100%;
    max-height: 350px;
    height: auto;
    object-fit: cover;
    margin-bottom: 0.75rem;
    transition: transform 0.4s ease;

    &:hover {
      transform: scale(1.05);
    }
  }
`;

const PhotoTitle = styled(Link)`
  display: inline-block;
  font-family: "Euclid Circular A", sans-serif;
  font-size: 1rem;
  margin-top: 0.5rem;
  color: #333;
  cursor: pointer;
  position: relative;
  text-decoration: none;
  transition: color 0.3s;

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -2px;
    transform: translateX(-50%);
    height: 1px;
    width: 0;
    background-color: #800000;
    transition: width 0.4s ease;
  }

  &:hover {
    color: #800000;
  }

  &:hover::after {
    width: 100%;
  }
`;

const Heading = styled.h1`
  font-size: 2.5rem;
  font-family: "Remains", "LC Chalk", serif;
  color: #000;
  margin-bottom: 1rem;
  transition: color 0.3s;

  &:hover {
    color: #800000;
  }
`;

const PhotoPage = () => {
  const photos = [
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Sonya" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Lisa" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "John" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Helen" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Brock" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Jessica" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Evgeniya" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Victoryia" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Veronica" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Nastya" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Hector" },
    { src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=", title: "Kole" },

  ];

  return (
    <PageWrapper>
      <Heading>Фото</Heading>
      <Gallery>
        {photos.map((photo, index) => (
          <PhotoCard key={index}>
            <img src={photo.src} alt={photo.title} />
            <PhotoTitle to={`/person/${photo.title}`}>{photo.title}</PhotoTitle>
          </PhotoCard>
        ))}
      </Gallery>
    </PageWrapper>
  );
};

export default PhotoPage;
