import React, { useEffect, useState } from "react";
import styled from "styled-components";
import AOS from "aos";
import "aos/dist/aos.css";
import ModalForm from "../components/ModalForm";
const PageWrapper = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
`;

const Heading = styled.h1`
  font-size: 2.5rem;
  font-family: "Remains", "LC Chalk", serif;
  color: #000;
  margin-bottom: 2rem;
  transition: color 0.3s;

  &:hover {
    color: #800000;
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
`;

const PriceCard = styled.div`
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  padding: 2rem;
  border-radius: 8px;
  transition: border-color 0.3s, transform 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;

  &:hover {
    border-color: #800000;
    transform: translateY(-5px);
  }
`;

const PriceTitle = styled.h2`
  font-size: 1.5rem;
  font-family: "Euclid Circular A", sans-serif;
  color: #000;
  margin-bottom: 1rem;
`;

const Price = styled.p`
  font-size: 2rem;
  font-weight: bold;
  color: #800000;
  margin-bottom: 1rem;
`;

const Description = styled.p`
  font-size: 1rem;
  color: #555;
  text-align: center;
`;

const Button = styled.a`
  margin-top: auto;
  padding: 0.75rem 1.5rem;
  border: 2px solid #800000;
  color: #800000;
  background-color: transparent;
  font-size: 1rem;
  font-family: "Euclid Circular A", sans-serif;
  text-decoration: none;
  transition: all 0.3s;
  margin-top: 2rem;
  border-radius: 4px;

  &:hover {
    background-color: #800000;
    color: #fff;
  }
`;

const Prices = () => {
    useEffect(() => {
      AOS.init({ duration: 1000 });
    }, []);
  
    const [isModalOpen, setModalOpen] = useState(false);
  
    return (
      <>
        <PageWrapper id="prices" >
          <Heading data-aos="fade-up">Прайс-лист</Heading>
  
          <Grid>
            <PriceCard data-aos="fade-up" data-aos-delay="100">
              <PriceTitle>Базовая съёмка</PriceTitle>
              <Price>от 15 000 ₽</Price>
              <Description>Идеально для коротких проектов и мероприятий.</Description>
              <Button onClick={() => setModalOpen(true)}>Заказать</Button>
            </PriceCard>
  
            <PriceCard data-aos="fade-up" data-aos-delay="200">
              <PriceTitle>Рекламный ролик</PriceTitle>
              <Price>от 35 000 ₽</Price>
              <Description>Киношная картинка, сценарий, полный продакшн.</Description>
              <Button onClick={() => setModalOpen(true)}>Заказать</Button>
            </PriceCard>
  
            {/* Другие карточки... */}
          </Grid>
  
          {/* Наш переиспользуемый попап */}
          <ModalForm isOpen={isModalOpen} onClose={() => setModalOpen(false)} />
        </PageWrapper>
      </>
    );
  };

export default Prices;
