import React from "react";
import { Link } from "react-scroll";
import styled from "styled-components";

const PageWrapper = styled.div`
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-between;
   max-width: 1200px;
   padding: 4rem 2rem;
   margin: 0 auto;

   @media (max-width: 768px) {
      flex-direction: column;
      gap: 2rem;
   }
`;

const TextSection = styled.div`
   flex: 1;
   padding-right: 2rem;
   cursor: pointer;
`;

const Heading = styled.h1`
   font-size: 2.75rem;
   margin-bottom: 1rem;
   color: #000;
   transition: color 0.3s ease;
   font-family: "Remains", "LC Chalk", serif;
   &:hover {
      color: #800000;
   }
`;
const Button = styled.button`
   margin-top: 2rem;
   padding: 0.75rem 2rem;
   font-size: 1rem;
   background-color: transparent;
   border: 2px solid #800000;
   color: #800000;
   font-family: "Euclid Circular A", sans-serif;
   cursor: pointer;
   transition: all 0.3s ease;

   &:hover {
      background-color: #800000;
      color: #fff;
   }
`;

const Paragraph = styled.p`
   font-size: 1.125rem;
   line-height: 1.6;
   margin-bottom: 1rem;
   transition: color 0.3s ease;
   font-family: "Euclid Circular A", sans-serif;
   &:hover {
      color: #800000;
   }
`;

const ImageSection = styled.div`
   flex: 1;
   text-align: right;

   img {
      max-width: 100%;
      height: auto;
      border: 1px solid #ccc;
      object-fit: cover;
   }
`;
const Stats = styled.div`
   display: flex;
   flex-wrap: wrap;
   gap: 2rem;
   margin-top: 2rem;

   div {
      font-size: 1.25rem;
      font-family: "Euclid Circular A", sans-serif;
      color: #000;

      span {
         display: block;
         font-size: 2rem;
         font-weight: bold;
         color: #800000;
      }
   }
`;

const About = () => {
   return (
      <>
         <PageWrapper id="about">
            <TextSection data-aos="fade-right">
               <Heading>Проффесиональный видеограф</Heading>
               <Paragraph>
                  Работаю в жанре классического кино и современного брендинга.
                  Мои кадры — это атмосфера, свет и чувство.
               </Paragraph>
               <Paragraph>
                  Каждая съёмка для меня — как постановка спектакля: от света до
                  последнего акцента в кадре.
               </Paragraph>
               <Paragraph>
                  Сотрудничаю с брендами, артистами и теми, кто ценит эстетику.
                  Готов к диалогу и новым историям.
               </Paragraph>

               <Stats>
                  <div>
                     <span>120+</span>
                     проектов завершено
                  </div>
                  <div>
                     <span>6 лет</span>
                     опыта в съёмке
                  </div>
               </Stats>

               <Link to="portfolio" smooth={true} duration={800}>
                  <Button>Посмотреть работы</Button>
               </Link>
            </TextSection>
            <ImageSection data-aos="fade-left">
               <img
                  src="https://www.format.com/wp-content/uploads/holding_video_camera.jpg"
                  alt="sdfdff"
               />
            </ImageSection>
         </PageWrapper>
      </>
   );
};

export default About;
