import React, { useState } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import Lightbox from "yet-another-react-lightbox";
import "yet-another-react-lightbox/styles.css";
const PageWrapper = styled.div`
   max-width: 1200px;
   margin: 0 auto;
   padding: 4rem 2rem;
`;

const Heading = styled.h1`
   font-size: 2.5rem;
   font-family: "Remains", "LC Chalk", serif;
   color: #000;
   margin-bottom: 1rem;
   transition: color 0.3s;

   &:hover {
      color: #800000;
   }
`;

const MarqueeWrapper = styled.div`
   overflow: hidden;
   white-space: nowrap;
   margin: 1.5rem 0 2.5rem;
   border-top: 1px solid #ccc;
   border-bottom: 1px solid #ccc;
   padding: 20px;
`;

const MarqueeInner = styled.div`
   display: inline-flex;
   animation: scroll-left 20s linear infinite;

   @keyframes scroll-left {
      0% {
         transform: translateX(0);
      }
      100% {
         transform: translateX(-50%);
      }
   }

   span {
      display: inline-block;
      margin-right: 3rem;
      font-family: "Euclid Circular A", sans-serif;
      font-size: 1rem;
      color: #000;
      transition: all 0.3s;
      position: relative;
      cursor: pointer;

      &:hover {
         color: #800000;
         transform: scale(1.1);
      }

      &:hover::after {
         content: "";
         position: absolute;
         left: 0;
         bottom: -2px;
         height: 1px;
         width: 100%;
         background-color: #800000;
      }
   }
`;

const Gallery = styled.div`
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
   gap: 1.5rem;
`;

const PhotoItem = styled.div`
   overflow: hidden;
   border: 1px solid #ccc;
   transition: border 0.3s;

   img {
      width: 100%;
      height: auto;
      display: block;
      object-fit: cover;
      transition: transform 0.4s ease;
      cursor: pointer;
   }

   &:hover img {
      transform: scale(1.1);
   }
`;
const Button = styled.button`
   margin-top: 2rem;
   padding: 0.75rem 2rem;
   font-size: 1rem;
   background-color: transparent;
   border: 2px solid #800000;
   color: #800000;
   font-family: "Euclid Circular A", sans-serif;
   cursor: pointer;
   transition: all 0.3s ease;

   &:hover {
      background-color: #800000;
      color: #fff;
   }
`;
const PortfolioPhoto = () => {
   const [open, setOpen] = useState(false);
   const [index, setIndex] = useState(0);

   const images = [
      {
         src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
      },
      {
         src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
      },
      {
         src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
      },
      {
         src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
      },
      {
         src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
      },
      {
         src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
      },
   ];

   return (
      <PageWrapper id="portfolio">
         <Heading>Фотопортфолио</Heading>

         <MarqueeWrapper>
            <MarqueeInner>
               <span>ifa</span>
               <span>Wedding Documentary</span>
               <span>Fashion Lookbook</span>
               <span>Backstage Vol.2</span>
               <span>Монохром</span>
               <span>Urban Stories</span>
               <span>Promo для Alfa</span>
               <span>ifa</span>
               <span>Wedding Documentary</span>
               <span>ifa</span>
               <span>Wedding Documentary</span>
               <span>Fashion Lookbook</span>
               <span>Backstage Vol.2</span>
               <span>Монохром</span>
               <span>Urban Stories</span>
               <span>Promo для Alfa</span>
               <span>ifa</span>
               <span>Wedding Documentary</span>
            </MarqueeInner>
         </MarqueeWrapper>

         <Gallery>
            {images.map((img, i) => (
               <PhotoItem
                  key={i}
                  onClick={() => {
                     setIndex(i);
                     setOpen(true);
                  }}
               >
                  <img src={img.src} alt={`Work ${i + 1}`} />
               </PhotoItem>
            ))}
         </Gallery>
         <Link to="/portfolio-photo">
            <Button>Еще</Button>
         </Link>
         <Lightbox
            open={open}
            close={() => setOpen(false)}
            slides={images}
            index={index}
         />
      </PageWrapper>
   );
};

export default PortfolioPhoto;
