import React, { useEffect } from "react";
import styled from "styled-components";
import AOS from "aos";
import "aos/dist/aos.css";

const PageWrapper = styled.div`
   max-width: 1200px;
   margin: 0 auto;
   padding: 4rem 2rem;
`;

const Heading = styled.h1`
   font-size: 2.5rem;
   font-family: "Remains", "LC Chalk", serif;
   color: #000;
   margin-bottom: 2rem;
   transition: color 0.3s;

   &:hover {
      color: #800000;
   }
`;

const Gallery = styled.div`
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
   gap: 2rem;
`;

const VideoCard = styled.div`
   position: relative;
   overflow: hidden;
   background-color: #111;
   border: 1px solid #ccc;
   border-radius: 8px;
   transition: border-color 0.3s, transform 0.4s ease;

   &:hover {
      border-color: #800000;
      transform: scale(1.02);
   }

   iframe {
      width: 100%;
      height: 200px;
      border: none;
   }

   @media (max-width: 768px) {
      iframe {
         height: 180px;
      }
   }
`;

const VideoTitle = styled.h3`
   font-size: 1.2rem;
   color: #000;
   margin-top: 0.5rem;
   font-family: "Euclid Circular A", sans-serif;
   transition: color 0.3s;

   &:hover {
      color: #800000;
   }
`;
const Button = styled.button`
   margin-top: 2rem;
   padding: 0.75rem 2rem;
   font-size: 1rem;
   background-color: transparent;
   border: 2px solid #800000;
   color: #800000;
   font-family: "Euclid Circular A", sans-serif;
   cursor: pointer;
   transition: all 0.3s ease;

   &:hover {
      background-color: #800000;
      color: #fff;
   }
`;

const PortfolioVideo = () => {
   useEffect(() => {
      AOS.init({ duration: 1000 });
   }, []);

   return (
      <PageWrapper>
         <Heading data-aos="fade-up">Видео-портфолио</Heading>

         <Gallery>
            <div data-aos="fade-up" data-aos-delay="100">
               <VideoCard>
                  <iframe
                     src="https://www.youtube.com/embed/QUx1anKHYuI"
                     title="Работа 1"
                     allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                     allowFullScreen
                  ></iframe>
               </VideoCard>
               <VideoTitle>Promo для Alfa</VideoTitle>
            </div>

            <div data-aos="fade-up" data-aos-delay="200">
               <VideoCard>
                  <iframe
                     src="https://www.youtube.com/embed/QUx1anKHYuI"
                     title="Работа 2"
                     allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                     allowFullScreen
                  ></iframe>
               </VideoCard>
               <VideoTitle>Fashion Lookbook</VideoTitle>
            </div>

            <div data-aos="fade-up" data-aos-delay="300">
               <VideoCard>
                  <iframe
                     src="https://www.youtube.com/embed/8bSn3qCICiw"
                     title="Работа 3"
                     allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                     allowFullScreen
                  ></iframe>
               </VideoCard>
               <VideoTitle>Backstage Vol.2</VideoTitle>
            </div>
         </Gallery>
         <Button>Еще</Button>
      </PageWrapper>
   );
};

export default PortfolioVideo;
