import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Link } from 'react-scroll';
import { motion, AnimatePresence } from 'framer-motion';

const HeaderWrapper = styled.header`
  position: absolute;
  top: 0;
  width: 100%;
  padding: 1.5rem 3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 20;
  background: transparent;
`;

const Logo = styled.img`
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
`;

const Nav = styled.nav`
  display: flex;
  gap: 2rem;
  align-items: center;

  a {
    position: relative;
    font-family: 'Euclid Circular A', sans-serif;
    color: white;
    font-size: 0.9rem;
    text-transform: uppercase;
    text-decoration: none;
    transition: color 0.3s ease;
    cursor: pointer;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 0;
      height: 2px;
      background-color: #800000;
      transition: width 0.3s ease;
    }

    &:hover {
      color: #800000;
    }

    &:hover::after {
      width: 100%;
    }
  }

  @media (max-width: 768px) {
    display: none;
  }
`;

const Burger = styled.div`
  display: none;
  flex-direction: column;
  cursor: pointer;
  z-index: 25;

  div {
    width: 30px;
    height: 3px;
    background: white;
    margin: 5px;
    transition: all 0.4s ease;
  }

  @media (max-width: 768px) {
    display: flex;
  }
`;

const MenuOverlay = styled(motion.div)`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #000; /* ЧЕРНЫЙ ФОН */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  z-index: 15;
`;

const MenuItem = styled(Link)`
  font-family: 'Euclid Circular A', sans-serif;
  color: white;
  font-size: 2rem;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  text-align: center; /* Центрируем текст */
  position: relative;
  transition: color 0.3s ease;

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -6px;
    width: 0;
    height: 2px;
    background-color: #800000;
    transition: width 0.3s ease;
  }

  &:hover {
    color: #800000;
  }

  &:hover::after {
    width: 100%;
  }
`;

const Header = () => {
  const [isOpen, setIsOpen] = useState(false);

  // Блокируем скролл при открытом меню
  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'auto';
    }
  }, [isOpen]);

  return (
    <>
      <HeaderWrapper>
        <Logo src="/assets/profile.jpg" alt="logo" />

        {/* Обычное меню на компьютерах */}
        <Nav>
          <Link to="about" smooth={true} duration={800}>О себе</Link>
          <Link to="portfolio" smooth={true} duration={800}>Портфолио</Link>
          <Link to="prices" smooth={true} duration={800}>Цены</Link>
          <Link to="contacts" smooth={true} duration={800}>Контакты</Link>
        </Nav>

        {/* Бургер кнопка на мобиле */}
        <Burger onClick={() => setIsOpen(!isOpen)}>
          <div style={{ transform: isOpen ? "rotate(45deg) translate(5px, 5px)" : "none" }} />
          <div style={{ opacity: isOpen ? 0 : 1 }} />
          <div style={{ transform: isOpen ? "rotate(-45deg) translate(5px, -5px)" : "none" }} />
        </Burger>
      </HeaderWrapper>

      {/* Бургер-меню на весь экран */}
      <AnimatePresence>
        {isOpen && (
          <MenuOverlay
            initial={{ y: "-100%" }}
            animate={{ y: 0 }}
            exit={{ y: "-100%" }}
            transition={{ duration: 0.6, ease: "easeInOut" }}
          >
            <MenuItem to="about" smooth={true} duration={800} onClick={() => setIsOpen(false)}>О себе</MenuItem>
            <MenuItem to="portfolio" smooth={true} duration={800} onClick={() => setIsOpen(false)}>Портфолио</MenuItem>
            <MenuItem to="prices" smooth={true} duration={800} onClick={() => setIsOpen(false)}>Цены</MenuItem>
            <MenuItem to="contacts" smooth={true} duration={800} onClick={() => setIsOpen(false)}>Контакты</MenuItem>
          </MenuOverlay>
        )}
      </AnimatePresence>
    </>
  );
};

export default Header;
