import React, { useState } from "react";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import Lightbox from "yet-another-react-lightbox";
import "yet-another-react-lightbox/styles.css";
const PageWrapper = styled.div`
  background-color: #fff;
  min-height: 100vh;
  color: #000;
  padding: 4rem 30px;
`;

const Heading = styled.h1`
  font-size: 2.5rem;
  font-family: "Remains", "LC Chalk", serif;
  color: #000;
  margin-bottom: 2rem;
  text-align: center;
  transition: color 0.3s;

  &:hover {
    color: #800000;
  }
`;

const Gallery = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  justify-items: center;
`;

const PhotoItem = styled.div`
  overflow: hidden;
  border: 1px solid #ccc;
  width: 100%;
  cursor: zoom-in; /* Курсор монокль */

  img {
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover;
    transition: transform 0.4s ease;

    &:hover {
      transform: scale(1.05);
    }
  }
`;

const PhotoDetailedPage = () => {
  const [open, setOpen] = useState(false);
  const [index, setIndex] = useState(0);
  const { name } = useParams();
  const images = [
    {
       src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
    },
    {
       src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
    },
    {
       src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
    },
    {
       src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
    },
    {
       src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
    },
    {
       src: "https://media.istockphoto.com/id/491783647/photo/asian-beauty.jpg?s=612x612&w=0&k=20&c=RauwXQ-fB5L7VWjEPxPX_jSNbJZmJ9SkQkJViLNgVNk=",
    },
 ];

  return (
    <PageWrapper>
      <Heading>{name}</Heading>

      <Gallery>
        {images.map((img, i) => (
          <PhotoItem
            key={i}
            onClick={() => {
              setIndex(i);
              setOpen(true);
            }}
          >
            <img src={img.src} alt={`Work ${i + 1}`} />
          </PhotoItem>
        ))}
      </Gallery>

      <Lightbox
        open={open}
        close={() => setOpen(false)}
        slides={images}
        index={index}
      />
    </PageWrapper>
  );
};

export default PhotoDetailedPage
