import React from "react";
import styled, { createGlobalStyle, keyframes } from "styled-components";

// Keyframe animations
const GlobalStyle = createGlobalStyle`
  body {
    margin: 0;
    font-family: "Euclid Circular A", sans-serif;
    background-color: #fff;
    color: #000;
  }

  @keyframes border-left {
    0% { height: 0; }
    100% { height: 100%; }
  }

  @keyframes border-bottom {
    0% { width: 0; }
    100% { width: 100%; }
  }

  @keyframes border-right {
    0% { height: 0; }
    100% { height: 100%; }
  }

  @keyframes border-top {
    0% { width: 0; }
    100% { width: 100%; }
  }
`;

const FooterWrapper = styled.footer`
  background-color: #111;
  color: #eee;
  padding: 4rem 2rem;
  margin-top: 4rem;
`;

const Container = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-direction: row;
    gap: 4rem;
  }
`;

const Left = styled.div`
  flex: 1;
  margin-bottom: 2rem;

  @media (min-width: 768px) {
    margin-bottom: 0;
  }
`;

const Heading = styled.h2`
  font-size: 3.5rem;
  font-family: "Remains", "LC Chalk", cursive;
  color: #fff;
  margin-bottom: 1rem;

  &:hover {
    color: #800000;
  }
`;

const ContactList = styled.ul`
  list-style: none;
  padding: 0;
  margin-top: 2rem;

  li {
    margin-bottom: 1rem;

    a {
      text-decoration: none;
      color: #ccc;
      font-size: 1.1rem;
      transition: color 0.3s;

      &:hover {
        color: #800000;
      }
    }
  }
`;

const Right = styled.div`
  flex: 1;
`;

const Form = styled.form`
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
`;

const InputWrapper = styled.div`
  position: relative;
`;

const AnimatedInput = styled.input`
  width: 100%;
  padding: 0.75rem 1rem;
  background: #222;
  border: none;
  color: #eee;
  font-size: 1rem;
  font-family: "Euclid Circular A", sans-serif;

  &::placeholder {
    color: #777;
  }

  &:focus {
    outline: none;
  }

  & ~ span {
    position: absolute;
    background-color: #800000;
  }

  & ~ .border-left {
    top: 0;
    left: 0;
    width: 1px;
    height: 0;
  }

  & ~ .border-bottom {
    bottom: 0;
    left: 0;
    width: 0;
    height: 1px;
  }

  & ~ .border-right {
    top: 0;
    right: 0;
    width: 1px;
    height: 0;
  }

  & ~ .border-top {
    top: 0;
    left: 0;
    width: 0;
    height: 1px;
  }

  &:focus ~ .border-left {
    animation: border-left 0.2s forwards;
  }

  &:focus ~ .border-bottom {
    animation: border-bottom 0.2s 0.2s forwards;
  }

  &:focus ~ .border-right {
    animation: border-right 0.2s 0.4s forwards;
  }

  &:focus ~ .border-top {
    animation: border-top 0.2s 0.6s forwards;
  }
`;

const AnimatedTextarea = styled.textarea`
  width: 100%;
  padding: 0.75rem 1rem;
  background: #222;
  border: none;
  color: #eee;
  font-size: 1rem;
  font-family: "Euclid Circular A", sans-serif;
  resize: vertical;
  height: 150px;

  &::placeholder {
    color: #777;
  }

  &:focus {
    outline: none;
  }

  & ~ span {
    position: absolute;
    background-color: #800000;
  }

  & ~ .border-left {
    top: 0;
    left: 0;
    width: 1px;
    height: 0;
  }

  & ~ .border-bottom {
    bottom: 0;
    left: 0;
    width: 0;
    height: 1px;
  }

  & ~ .border-right {
    top: 0;
    right: 0;
    width: 1px;
    height: 0;
  }

  & ~ .border-top {
    top: 0;
    left: 0;
    width: 0;
    height: 1px;
  }

  &:focus ~ .border-left {
    animation: border-left 0.2s forwards;
  }

  &:focus ~ .border-bottom {
    animation: border-bottom 0.2s 0.2s forwards;
  }

  &:focus ~ .border-right {
    animation: border-right 0.2s 0.4s forwards;
  }

  &:focus ~ .border-top {
    animation: border-top 0.2s 0.6s forwards;
  }
`;

const Button = styled.button`
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: transparent;
  border: 1px solid #800000;
  color: #800000;
  font-family: "Euclid Circular A", sans-serif;
  cursor: pointer;
  transition: all 0.3s;

  &:hover {
    background-color: #800000;
    color: #fff;
  }
`;

const Contacts = () => {
  return (
    <>
      <GlobalStyle />
      <FooterWrapper id="contacts" >
        <Container>
          <Left>
            <Heading>Связаться со мной</Heading>
            <ContactList>
              <li><a href="mailto:youremail@example.com">youremail@example.com</a></li>
              <li><a href="tel:+79999999999">+7 (999) 999-99-99</a></li>
              <li><a href="https://instagram.com/yourprofile" target="_blank" rel="noreferrer">Instagram / @yourprofile</a></li>
              <li><a href="https://t.me/yourtelegram" target="_blank" rel="noreferrer">Telegram / @yourtelegram</a></li>
            </ContactList>
          </Left>

          <Right>
            <Form>
              <InputWrapper>
                <AnimatedInput type="text" placeholder="Ваше имя" required />
                <span className="border-left" />
                <span className="border-bottom" />
                <span className="border-right" />
                <span className="border-top" />
              </InputWrapper>

              <InputWrapper>
                <AnimatedInput type="email" placeholder="Email" required />
                <span className="border-left" />
                <span className="border-bottom" />
                <span className="border-right" />
                <span className="border-top" />
              </InputWrapper>

    

              <InputWrapper>
                <AnimatedTextarea placeholder="Ваше сообщение..." required />
                <span className="border-left" />
                <span className="border-bottom" />
                <span className="border-right" />
                <span className="border-top" />
              </InputWrapper>

              <Button type="submit">Отправить</Button>
            </Form>
          </Right>
        </Container>
      </FooterWrapper>
    </>
  );
};

export default Contacts;
